<template>
  <carousel v-if="events" :items-to-show="1" :wrap-around="true" ref="carousel">
    <slide v-for="event in events" :key="event.id">
      <img
        :src="event.image[0].url"
        :alt="event.image[0].alternativeText"
        class="event-img"
      />
      <div class="event-content">
        <h2>{{ event.title }}</h2>
        <p>
          {{
            event.description.length < stringLimit
              ? event.description
              : event.description.substring(0, stringLimit) + "..."
          }}
        </p>
      </div>
    </slide>

    <template #addons>
      <navigation />
      <pagination />
    </template>
  </carousel>
  <div v-else></div>
</template>

<script>
import axios from "axios";

// If you are using PurgeCSS, make sure to whitelist the carousel CSS classes
import "vue3-carousel/dist/carousel.css";
import { Carousel, Slide, Pagination, Navigation } from "vue3-carousel";

export default {
  name: "App",
  components: {
    Carousel,
    Slide,
    Pagination,
    Navigation,
  },
  data() {
    return {
      apiURL: "https://tabletopid-cms.herokuapp.com",
      events: null,
      stringLimit: window.matchMedia("(min-width: 992px)").matches ? 360 : 120,
    };
  },
  mounted() {
    axios
      .get(`${this.apiURL}/events?ongoing=true&_sort=id:ASC`)
      .then((response) => {
        this.events = response.data;
      });
  },
  created() {
    window.addEventListener("resize", this.mediaQuery);
    console.log("resize event added");
  },
  methods: {
    mediaQuery() {
      console.log("window resized");
      if (window.matchMedia("(min-width: 992px)").matches)
        this.stringLimit = 360;
      else {
        this.stringLimit = 120;
      }
    },
  },
};
</script>

<style scoped>
.carousel__slide {
  user-select: none;
  cursor: move;
  cursor: grab;
  display: flex;
  flex-flow: column;
  height: 400px;
  width: 100%;
  background-color: rgb(225, 225, 225);
}

.carousel__slide:active {
  cursor: grabbing;
}

.carousel__pagination {
  position: absolute;
  bottom: 2px;
  left: 50%;
  padding: 0;
  padding: 0;
  transform: translate(-50%, -50%);
}

.event-img {
  width: 120px;
  height: 120px;
}

.event-content {
  max-width: 60%;
  text-align: justify;
}

@media only screen and (min-width: 992px) {
  .carousel__slide {
    flex-flow: row;
    height: 400px;
    width: 100%;
  }

  .event-img {
    width: 240px;
    height: 240px;
    margin-right: 40px;
  }
}
</style>