<template>
  <div class="filter-form">
    <fieldset>
      <div id="label">
        <h3>Filters</h3>
        <button @click.stop.prevent="clearFilters">Clear All</button>
      </div>
      <div>
        <button
          id="publisher"
          class="collapser"
          @click.stop.prevent="transition($event)"
        >
          Publisher
        </button>
        <div class="collapsible">
          <div v-for="publisher in publishers" :key="publisher.id">
            <input
              type="radio"
              :id="publisher.value"
              :value="publisher.value"
              v-model="filter_data.publisher"
              @change="$emit('filter', filter_data)"
            />
            <label :for="publisher.value">{{ publisher.name }}</label>
          </div>
        </div>
      </div>
      <div>
        <button
          id="type"
          class="collapser"
          @click.stop.prevent="transition($event)"
        >
          Tipe
        </button>
        <div class="collapsible">
          <div v-for="type in types" :key="type.id">
            <input
              type="radio"
              :id="type.value"
              :value="type.value"
              v-model="filter_data.type"
              @change="$emit('filter', filter_data)"
            />
            <label :for="type.value">{{ type.name }}</label>
          </div>
        </div>
      </div>
      <div>
        <button
          id="price"
          class="collapser"
          @click.stop.prevent="transition($event)"
        >
          Harga
        </button>
        <div class="collapsible">
          <input
            type="number"
            name="floor"
            id="floor"
            min="0"
            v-model="filter_data.price_floor"
            @keyup.enter="$emit('filter', filter_data)"
          />
          <p>-</p>
          <input
            type="number"
            name="roof"
            id="roof"
            min="0"
            v-model="filter_data.price_roof"
            @keyup.enter="$emit('filter', filter_data)"
          />
        </div>
      </div>
    </fieldset>
  </div>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      publishers: null,
      types: null,
      apiURL: "https://tabletopid-cms.herokuapp.com",
      filter_data: {
        publisher: null,
        type: null,
        price_floor: null,
        price_roof: null,
        product_name: null,
      },
    };
  },
  methods: {
    transition(e) {
      const target = e.target.nextElementSibling;
      e.target.classList.toggle("active");

      if (target.style.maxHeight) {
        target.style.maxHeight = null;
      } else {
        target.style.maxHeight = target.scrollHeight + "px";
      }
    },
    clearFilters() {
      for (let input of this.$el.querySelectorAll(
        "input[type='radio']:checked"
      )) {
        input.checked = false;
      }

      for (let input of this.$el.querySelectorAll("input[type='number']")) {
        input.value = null;
      }

      for (let filter in this.filter_data) {
        this.filter_data[filter] = null;
      }

      this.$emit("clear-filters");
    },
  },
  mounted() {
    axios
      .get(`${this.apiURL}/publishers`)
      .then((response) => {
        this.publishers = response.data;
      })
      .catch((error) => console.error(error));
    axios
      .get(`${this.apiURL}/types`)
      .then((response) => {
        this.types = response.data;
      })
      .catch((error) => console.error(error));
  },
  watch: {
    searchQuery(val) {
      this.filter_data.product_name = val;
      this.$emit("filter", this.filter_data);
    },
    sortQuery(val) {
      console.log("this changes");
      this.$emit("filter", this.filter_data);
    },
  },
  props: {
    searchQuery: String,
    sortQuery: Array,
  },
};
</script>

