<template>
  <header>
    <NavigationBar />
    <Slider></Slider>
  </header>
  <main>
    <input type="text" name="cat-search" id="cat-search" v-model="search_input"/>
    <div class="top">
      <h5 class="count">{{product_count}} Results</h5>
      <div class="sort">
        <h5>Sort By:</h5>
        <select name="sort-type" id="sort-type" v-model="sort_type">
          <option value='["id", "ASC"]'>Relevansi</option>
          <option value='["published_at", "DESC"]'>Terbaru</option>
          <option value='["sales", "DESC"]'>Popularitas</option>
          <option value='["price", "ASC"]'>Harga (Low to High)</option>
          <option value='["price", "DESC"]'>Harga (High to Low)</option>
        </select>
      </div>
    </div>
    <div class="middle">
      <div class="clickables">
        <Filter @filter="queryProducts" @clear-filters="clearFilters" :search-query="search_input" :sort-query="sort_type"/>
        <div class="table-toggler">
          <label for="toggle-table">Beralih ke tampilan tabel</label>
          <input type="checkbox" v-model="toggleTable" id="toggle-table">
        </div>
        <div class="p3-btn" :class="{windows: isWindows}">
          <p>Produk per halaman:</p>
          <button @click="changeProductsPerPage(this.pageProdCount * 1)">{{pageProdCount * 1}}</button>
          <button @click="changeProductsPerPage(this.pageProdCount * 2)">{{pageProdCount * 2}}</button>
          <button @click="changeProductsPerPage(this.pageProdCount * 3)">{{pageProdCount * 3}}</button>
          <button @click="changeProductsPerPage(this.pageProdCount * 4)">{{pageProdCount * 4}}</button>
        </div>
      </div>
      <div v-if="!toggleTable" class="items">
        <a v-for="product in productsDisplayed" :key="product.id" :href="'products?id=' + product.id">
          <div class="item">
            <img
              class="item-img"
              :src="product.image[0].url"
              :alt="product.image[0].alternativeText"
            />
            <div class="item-detail">
              <h5 class="item-publisher">{{ product.publisher.name }}</h5>
              <h4 class="item-title">{{ product.title }}</h4>
              <h3 class="item-price">Rp.{{ product.price.toString().replace(/\B(?<!\.\d*)(?=(\d{3})+(?!\d))/g, ".") }},00</h3>
              <hr />
              <div class="item-types"><p>{{ product.type.name }}</p></div>
            </div>
          </div>
        </a>
      </div>
      <div v-else class="catalogue-table"><table>
        <colgroup>
          <col class="table-name">
          <col v-if="isDesktop" class="table-publisher">
          <col v-if="isDesktop" class="table-type">
          <col class="table-price">
        </colgroup>
        <thead><tr>
        <th>Nama Produk</th>
        <th v-if="isDesktop">Publisher</th>
        <th v-if="isDesktop">Tipe</th>
        <th>Harga</th>
      </tr></thead><tbody><tr v-for="product in productsDisplayed" :key="product.id"><td><a :href="'products?id=' + product.id">{{product.title}}</a></td>
      <td v-if="isDesktop">{{product.publisher.name}}</td>
      <td v-if="isDesktop">{{product.type.name}}</td>
      <td>Rp.{{ product.price.toString().replace(/\B(?<!\.\d*)(?=(\d{3})+(?!\d))/g, ".") }},00</td></tr></tbody></table></div>
    </div>
    <div class="bottom">
      <nav>
        <button @click="changePage(1)">First</button><button @click="changePage(page - 1)">Previous</button>
        <h5 v-if="products">Page {{page}}/{{productsCeil}}</h5>
        <h5 v-else>Page 1/1</h5>
        <button @click="changePage(page + 1)">Next</button><button @click="changePage(productsCeil)">Last</button>
      </nav>
    </div>
  </main>
  <footer><Footer /></footer>
</template>

<script>
import qs from "qs";
import axios from "axios";

import NavigationBar from "../../components/NavigationBar.vue";
import Footer from "../../components/Footer.vue";
import Filter from "../../components/Filter.vue";
import Slider from "../../components/Slider.vue";

export default {
  name: "App",
  components: {
    NavigationBar,
    Footer,
    Filter,
    Slider,
  },
  data() {
    return {
      productsPerPage: 16,
      page: 1,
      toggleTable: false,
      sort_type: [],
      product_count: 0,
      products: null,
      productsDisplayed: [],
      productsCeil: 1,
      apiURL: "https://tabletopid-cms.herokuapp.com",
      search_input: null,
      isDesktop: window.matchMedia("(min-width: 992px)").matches,
    };
  },
  mounted() {
    this.queryProducts();
  },
  computed: {
    productsLimit() {
      return this.productsPerPage * this.page;
    },
    isWindows() {
      if (navigator.userAgent.indexOf("Win") != -1) {
        this.productsPerPage = 12;
        return true;
      }
    },
    pageProdCount() {
      if (this.isWindows) {
        return 3;
      } else {
        return 4;
      }
    },
  },
  methods: {
    queryProducts(filterData) {
      let publisher = null;
      let type = null;
      let price_roof = null;
      let price_floor = null;
      let product_name = null;
      let sort_query = "";

      if (filterData) {
        publisher = filterData["publisher"];
        type = filterData["type"];
        price_roof = filterData["price_roof"];
        price_floor = filterData["price_floor"];
        product_name = filterData["product_name"];
      }

      if (this.sort_type.length != 0) {
        const sortType = JSON.parse(this.sort_type);
        sort_query = `&_sort=${sortType[0]}:${sortType[1]}`;
      }

      const query = qs.stringify(
        {
          _where: [
            {
              "publisher.value": publisher,
              "type.value": type,
              price_gte: price_floor,
              price_lte: price_roof,
              title_contains: product_name,
            },
          ],
          _sort: {},
        },
        { skipNulls: true }
      );

      axios
        .get(`${this.apiURL}/products?${query}${sort_query}`)
        .then((response) => {
          this.products = response.data;
          this.product_count = response.data.length;
          this.displayProducts(this.products);
          this.productsCeil = Math.ceil(
            this.products.length / this.productsPerPage
          );
          if (this.page > this.productsCeil) {
            this.page = this.productsCeil;
          } else if (this.page === 0 && this.product_count) {
            this.page = 1;
          }
        })
        .catch((error) => (this.products = {}));
    },
    clearFilters() {
      this.search_input = null;
      this.queryProducts();
    },
    changeProductsPerPage(value) {
      this.productsPerPage = value;
      if (this.page > Math.ceil(this.products.length / this.productsPerPage)) {
        this.page = Math.ceil(this.products.length / this.productsPerPage);
      }
    },
    displayProducts(products) {
      this.productsDisplayed = [];
      products.forEach((product, index) => {
        if (
          index >= this.productsLimit - this.productsPerPage &&
          index < this.productsLimit
        ) {
          this.productsDisplayed.push(product);
        }
      });
      this.productsCeil = Math.ceil(
        this.products.length / this.productsPerPage
      );
    },
    changePage(newPage) {
      if (newPage === 0) {
        return;
      } else if (newPage > this.productsCeil) {
        return;
      }
      this.page = newPage;
    },
  },
  watch: {
    productsPerPage() {
      this.displayProducts(this.products);
    },
    page() {
      this.displayProducts(this.products);
    },
  },
  created() {
    window.addEventListener("resize", () => {
      this.isDesktop = window.matchMedia("(min-width: 992px)").matches;
    });
  },
};
</script>

